@import"https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&family=Titan+One&display=swap";@import'~node_modules/modern-normalize/modern-normalize.css';:root{--body-text-color:#000000;--primary-text-color: #ffffff;--secondary-text-color: #907E82;--btn-text-color: #D41443;--title-text-color:#FFA5BA;--accent-btn-color: #ecebeb;--secondary-accent-color: #D41443;--body-bg-color: #ffffff;--bg-product-1-color: #FFB8CA;--bg-product-2-color: #F0D1A5;--bg-product-3-color: #C2E297;--bg-about: #FFF5F6;--bg-footer: #FFF5F6;--primary-bg-hero: #FFB8CA;--secondary-bg-hero: #FFA5BA;--footer-border-color: #e1e1e1;--timing-function: cubic-bezier(0.4, 0, 0.2, 1)}.visually-hidden{position:absolute;width:1px;height:1px;margin:-1px;border:0;padding:0;white-space:nowrap;-webkit-clip-path:inset(100%);clip-path:inset(100%);clip:rect(0 0 0 0);overflow:hidden}html{-webkit-box-sizing:border-box;box-sizing:border-box}*,*::after,*::before{-webkit-box-sizing:inherit;box-sizing:inherit}h1,h2,h3,h4,h5,h6,p{margin-top:0}.list{list-style:none;padding:0;margin:0}.link{text-decoration:none;color:inherit}a{text-decoration:none;font-style:normal}li{list-style:none}img{display:block;max-width:100%;height:auto}address{font-style:normal}body{font-family:"DM Sans",sans-serif;font-weight:500;font-size:14px;line-height:1.76;letter-spacing:.04em;background:var(--body-bg-color);color:var(--body-text-color)}body.modal-open{overflow:hidden}body.menu-open{overflow:hidden}.section{padding-top:60px;padding-bottom:60px}.container{padding:0 15px;margin-left:auto;margin-right:auto}@media screen and (min-width: 320px){.container{width:320px;padding:0 20px;margin-left:auto;margin-right:auto}}@media screen and (min-width: 768px){.container{width:768px;padding:0 32px;margin-left:auto;margin-right:auto}}@media screen and (min-width: 1200px){.container{width:1200px;padding:0 15px;margin-left:auto;margin-right:auto}}