
.container {
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
  
  @media screen and (min-width: 320px) {
  width: 320px ;
  padding: 0 20px;
  margin-left: auto;
  margin-right: auto;
  }

  @media screen and (min-width: 768px) {
  width: 768px ;
  padding: 0 32px;
  margin-left: auto;
  margin-right: auto;
  }

  @media screen and (min-width: 1200px) {
  width: 1200px ;
  padding: 0 15px;
  margin-left: auto;
  margin-right: auto;
  }
}
